import {randomID} from '../utilities';

class Bill {
  constructor(a) {
    this.id =  randomID();
    this.order_id = a.order_id;
    this.ticket_id = a.ticket_id;
    this.items = a.items;
    this.total = +this.items.price * +this.items.quantity;
    this.created_time = Date.now();
    this.completed_time = null;
    this.is_done = false;
    this.type = a.type;
  }
}
export default Bill;



